<template>
  <div class="container text-left py-4">
    <div>text:: {{ apple }}</div>
    <div>Html content load :: <span v-html="channel"></span></div>
    <div :id="idName" class="boxBorder">Id Bind</div>
    <div :class="ClassName" class="classname2">Multiple Class Bind</div>
    <button :disabled="isDisabled">Attribute v bind</button>
    <h4 :class="isPromoted && 'className3'">
      promoted movie if true then text=yellow
    </h4>
    <h4 :class="isSoldout ? 'className4' : 'className5'">
      Soldout true=Red otherwise Green
    </h4>
    <div :class="['className4', 'className5']">Multiple class with Array</div>
    <div
      :class="[
        isPromoted && 'className3',
        isSoldout ? 'className4' : 'className5',
      ]"
    >
      Class bind with multiple condition(Array Approch)
    </div>

    <!-- classname : conditions  -->
    <div
      :class="{
        className4: isSoldout,
        className3: !isPromoted,
        underLine: isSoldout,
      }"
    >
      Class bind with multiple condition(Object Approch)
    </div>
    <!-- {style property: data Variable Name } -->
    <!-- style property name multiple word hole chemmel case -->
    <div class="boxBorder">
      <div
        :style="{
          background: HighlightColor,
          fontSize: fontSmall + 'rem',
          padding: '20px',
        }"
      >
        multiple Style Bind(object way)
      </div>

      <div :style="StyleObject">multiple Style Bind(2)</div>
      <div :style="[StyleObject, StyleObject2]">
        multiple Style Bind(array way),last object get priory
      </div>
    </div>
    <!-- ///////////////////////////////////////////// -->
    <h4>Conditional rendering:</h4>
    <div class="boxBorder">
      <div v-if="number === 0">The number is Zero</div>
      <div v-else-if="number < 0">The number is negative</div>
      <div v-else-if="number > 0">The number is possitive</div>
      <div v-else>Not a number</div>
    </div>

    <!-- //////////////////////////////////////////////////  -->
    <h4>content show:</h4>
    <div class="boxBorder">
      <template v-if="contentDisplay">here i am</template>
    </div>
    <div class="boxBorder">
      |
      <p v-show="hidden">here i am2</p>
    </div>
    <!-- ////////////////////////////////////////////// -->
    <h4>For loop:(array object)</h4>
    <div class="boxBorder">
      <template v-for="(name, index) in fullNames" :key="index">
        <div>
          {{ name.first }}
          <span v-for="(lastName, index) in name.last" :key="index">
            {{ lastName }}
          </span>
        </div>
      </template>
    </div>
    <h4>For loop:(array print with key name)</h4>
    <div class="boxBorder">
      <template v-for="(value, key, index) in fullNames2" :key="value">
        <div>index:: {{ index }} , Key Name: {{ key }} ,value: {{ value }}</div>
      </template>
    </div>
    <!-- /////////////////////////////////////////////// -->
    <h4>functions</h4>
    <div class="boxBorder">
      <div>Sum:: {{ 1 + 1 + 1 }}</div>
      <div>Add method:: {{ add() }}</div>
      <div>peramiterAdd method:: {{ peramiterAdd(1, 1, 1) }}</div>
      <div>data and method variables sum :: {{ add2(10) }}</div>
    </div>
    <div class="boxBorder centerContent">
      <h4>Counter with watchers</h4>

      <h1>{{ count }}</h1>
      <div class="d-flex">
        <button @click="increment">Increment</button>

        <button v-on:click="decrement">Decrement</button>
      </div>
    </div>
    <div class="boxBorder">
      <h4>
        Watcher : wather function will run immidiatly, on page load or component
        load (check the console after every load) & (Template Refs : use for
        input focus)
      </h4>
      <input type="text" v-model="movie" ref="inputRef" />
      <!-- ref="inputRef" is reference for creating focus on a input element -->
      <!-- inputRef property on mounted function  -->
    </div>
    <div class="boxBorder">
      <h4>
        Watcher :write code for define the wather for object or array data
        (deep: true)
      </h4>
      <input type="text" v-model="movieList.movie2" />
      <input type="text" v-model="movieList.movie3" />
    </div>
    <h4>Event catch</h4>
    <button @click="eventFunction">Alert</button>
    <div>
      <button @click="increment5(5, $event)">
        Increment(5) with event peramiter
      </button>
    </div>
    <!-- ////////////////////////////////////// -->
    <div class="boxBorder d-flex">
      <form
        class="boxBorder px-4"
        @submit.prevent="submitForm1"
        style="width: 50%"
      >
        <div class="form-group">
          <label class="text-danger" for="sel1"
            >##Use Control + click to multiple select:</label
          >
          <select
            class="form-control"
            id="sel1"
            multiple
            v-model.trim="formValues.selectedOptions"
          >
            <option value="india">india</option>
            <option value="purtugal">purtugal</option>
            <option value="pakistan">pakistan</option>
          </select>
        </div>

        <div class="form-group form-check">
          <input
            type="checkbox"
            class="form-check-input"
            id="exampleCheck1"
            v-model="formValues.checkbox"
            true-value="yes"
            false-value="no"
          />
          <label class="form-check-label" for="exampleCheck1"
            >Check me out</label
          >
        </div>
        <div class="form-group boxBorder py-3">
          <!-- v-model="formValues.multiSkills" -->
          <label class="form-check-label mr-3">Multiple skill check::</label>
          <div class="form-check form-check-inline">
            <input
              class="form-check-input"
              type="checkbox"
              id="inlineCheckbox1"
              value="html"
              v-model="formValues.multiSkills"
            />
            <label class="form-check-label" for="inlineCheckbox1">html</label>
          </div>
          <div class="form-check form-check-inline">
            <input
              class="form-check-input"
              type="checkbox"
              id="inlineCheckbox2"
              value="css"
              v-model="formValues.multiSkills"
            />
            <label class="form-check-label" for="inlineCheckbox2">css</label>
          </div>
        </div>
        <h4>##Modifier</h4>
        <div class="form-group">
          <label for="exampleInputEmail1"
            >Number modifier (v-model.number)
          </label>
          <input
            type="number"
            class="form-control"
            id="exampleInputEmail1"
            v-model.number="formValues.numberStore"
          />
        </div>
        <div class="form-group">
          <label for="Lazy"
            >Lazy modifier (v-model.lazy) (if click outside the input then value
            got stored)
          </label>
          <input
            type="number"
            class="form-control"
            id="Lazy"
            v-model.number.lazy="formValues.numberStore"
          />
        </div>
        <div class="form-group">
          <label for="enterAnything">Enter Form submit </label>
          <input
            type="text"
            class="form-control"
            id="enterAnything"
            v-model="formValues.enterAnything"
            @keyup.enter="submitForm1"
          />
        </div>

        <button class="btn btn-info">Submit</button>
      </form>
      <h4 class="boxBorder my-auto" style="height: 14rem; width: 48%">
        Form Data: {{ JSON.stringify(formValues, null, 2) }}
      </h4>
    </div>

    <!-- ////////////////////////////// -->

    <div class="boxBorder centerContent">
      <h4>v-once Use</h4>
      =render just 1 time. no event call can change them
      <h1 v-once>{{ count }}</h1>
      <div class="d-flex">
        <button @click="increment">Increment</button>

        <button v-on:click="decrement">Decrement</button>
      </div>
    </div>

    <div class="boxBorder centerContent">
      <h4>v-pre Use</h4>
      =render just as a code
      <h1 v-pre>{{ count }}</h1>
    </div>
    <div class="boxBorder">
      <h4>Use of computed method: fullname=</h4>
      {{ computedFullName }}
    </div>
    <div class="boxBorder centerContent">
      <h4>Use of computed get+ set : fullname=</h4>
      <b>{{ computedFullName2 }}</b>
      <div>
        <button class="btn btn-info" @click="changeComputedFullName2">
          change This Fullname
        </button>
      </div>
    </div>
    <!-- ////////////////////// -->
    <div class="boxBorder">
      <h4>Use of Props (Array Props)</h4>
      <Greet name="Boddruzzaman" :greet="greet" />
    </div>
    <!-- ///////////////////////////////// -->
    <div class="boxBorder">
      <h4>Use of Props (object Props)</h4>
      <Greet2 title="SJJJ g" :like="50" :isPublished="true" />
      <small>
        like and isPublished is( number and boolean) value.so we need to bind it
        .otherwise Vue took it as String value
      </small>
    </div>
    <!-- ///////////////////////////////// -->
    <div class="boxBorder">
      <h4>Non Prop Attributes</h4>
      <small>
        if we want to add an id attribute to a specific tagg , to the child
        component
      </small>
      <Greet3 id="here_my_id" />
    </div>
    <!-- ///////////////////////////////// -->
    <div class="boxBorder">
      <h4>Provide and Inject:</h4>
      <small> Sort way to pass the props to a child Component </small>
      <div>
        Logged in User Email from MixContent page (parent Component):
        {{ userEmail }}
      </div>
      <InjectExample />
    </div>
    <!-- ///////////////////////////////// -->
    <div class="boxBorder">
      <h4>Component Events / custom events + Validating Emitted Events:</h4>
      <small>
        if child Component need to pass a function to parent component
      </small>
      <div>
        <button class="btn btn-primary" @click="showPopup = true">
          Show Popup
        </button>
        <Popup v-show="showPopup" @popupClose="closePopup" />
        <!-- 
       #v-show er vitorer condition true hoye only tokhon show hobe, initially showPopup=false kora
     # @popupClose is custom event ,passed by popup component page
      --></div>
    </div>
    <!-- ///////////////////////////////// -->
    <div class="boxBorder">
      <h4>Custom input called from another component :(v-model)</h4>

      <InputExample v-model="inputName" />
      <div class="text-danger">Not working part</div>
    </div>
    <!-- ///////////////////////////////// -->
    <div class="boxBorder">
      <h4>Slots (To pass html or image on a child component)</h4>
      <template class="d-flex">
        <Card>
          <img
            class="card-img-top"
            :src="imageLink"
            alt="Card image"
            style="width: 100%"
          />
        </Card>
        <Card>
          <h4 class="text-danger bg-info">html content render</h4>
        </Card>
        <Card></Card>
        <Card>
          <template v-slot:peraText>
            <h4 class="card-text text-danger bg-dark">
              Slot By Name condition
            </h4>
          </template>
        </Card>
      </template>
    </div>
    <!-- ///////////////////////////////// -->
    <!-- ///////////////////////////////// -->
    <div class="boxBorder">
      <h4>
        Slot Props(To pass props from child component to parent and render the
        props conditionally)
      </h4>
      <NameList>
        <template v-slot:default="slotProps">
          <!-- ( v-slot:default) default= for there is no name for this slot . if there is any name then we will the 'default' word -->
          Full name:: {{ slotProps.firstName }} {{ slotProps.lastName }}
        </template>
      </NameList>

      <NameList>
        <template v-slot:default="slotProps">
          Last Name: {{ slotProps.firstName }}
        </template>
      </NameList>
    </div>
    <!-- ///////////////////////////////// -->
    <div class="boxBorder">
      <h4>Dynamic Components(conditionaly multiple component Load)</h4>
      <small>
        Also code of Keeping Dynamic Components Alive( keep-alive:: for Data
        cash of the components)</small
      >

      <div class="d-flex">
        <button @click="activeTab = 'TabA'">Tab A</button>
        <button @click="activeTab = 'TabB'">Tab B</button>
        <button @click="activeTab = 'TabC'">Tab C</button>
      </div>
      <div class="boxBorder">
        <keep-alive>
          <component :is="activeTab" />
        </keep-alive>
      </div>
    </div>
    <!-- ///////////////////////////////// -->
    <div class="boxBorder">
      <div>
        <h4>teleport tag:: will transfer it to an another id element</h4>
      </div>
      <!-- Button trigger modal -->
      <button
        type="button"
        class="btn btn-primary"
        data-toggle="modal"
        data-target="#exampleModal2"
      >
        Launch demo modal
      </button>
      <!-- teleport will transfer it to an another id element -->

      <teleport to="#modalDiv">
        <TeleportComponent />
      </teleport>
    </div>
    <!-- ///////////////////////////////// -->

    <postList ref="userListRef" />

    <!-- ///////////////////////////////// -->
    <Parent />
    <!-- ////////////////////////////// -->
    <div class="boxBorder  d-flex">
      <h4>Use Of Mixins::</h4>
      <MouseOver />
      <ClickCounter />
    </div>
  </div>
</template>

<script>
import Greet from "./MixContentInc/Greet.vue";
import Greet2 from "./MixContentInc/Greet2.vue";
import Greet3 from "./MixContentInc/Greet3.vue";
import InjectExample from "./MixContentInc/InjectExample.vue";
import Popup from "./MixContentInc/Popup.vue";
import InputExample from "./InputExample.vue";
import Card from "./Card.vue";
import NameList from "./NameList.vue";
import TabA from "./MixContentInc/Tab/TabA.vue";
import TabB from "./MixContentInc/Tab/TabB.vue";
import TabC from "./MixContentInc/Tab/TabC.vue";
import TeleportComponent from "./MixContentInc/TeleportComponent.vue";

import postList from "./MixContentInc/postList.vue";
import Parent from "./LifeCycle/Parent.vue";
import MouseOver from "./MixContentInc/Counter/MouseOver.vue";
import ClickCounter from "./MixContentInc/Counter/ClickCounter.vue";

export default {
  name: "MixContent",
  components: {
    Greet,
    Greet2,
    Greet3,
    InjectExample,
    Popup,
    InputExample,
    Card,
    NameList,
    TabA,
    TabB,
    TabC,
    TeleportComponent,
    postList,
    Parent,
    MouseOver,
    ClickCounter,
  },
  data() {
    return {
      apple: "this is an Apple",
      channel: `<b>codevolution</b> and <h4>hello world </h4>`,
      idName: "blahblah",
      ClassName: "blahblah2",
      isDisabled: true,
      isPromoted: false,
      isSoldout: true,
      HighlightColor: "orange",
      fontSmall: 0.75,
      StyleObject: {
        // style property : value
        background: "orange",
        fontSize: ".75rem",
        padding: "20px",
        marginTop: "3px",
      },
      StyleObject2: {
        // style property : value
        color: "gray",
      },
      number: 0,
      contentDisplay: true,
      hidden: false,
      fullNames: [
        {
          first: "Princess",
          last: ["Diana-", "indigo"],
        },
        {
          first: "Clark",
          last: ["Kent-", "indigo"],
        },
      ],
      fullNames2: {
        first: "Princess",
        last: "Diana",
      },
      baseNumber: 10,
      count: 0,
      count2: 0,

      formValues: {
        selectedOptions: [],
        checkbox: "no",
        multiSkills: [],
        numberStore: null,
        enterAnything: "",
      },
      firstName2: "Mirza",
      lastName2: "Khatun",
      movie: "hello hello",
      movieList: {
        movie2: "",
        movie3: "",
      },
      greet: "Welcome",
      userEmail: "mahesh@gmail.com",
      showPopup: false,
      inputName: "",
      imageLink:
        "data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22283%22%20height%3D%22160%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20283%20160%22%20preserveAspectRatio%3D%22none%22%3E%3Cdefs%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E%23holder_17acafb068e%20text%20%7B%20fill%3Argba(255%2C255%2C255%2C.75)%3Bfont-weight%3Anormal%3Bfont-family%3AHelvetica%2C%20monospace%3Bfont-size%3A14pt%20%7D%20%3C%2Fstyle%3E%3C%2Fdefs%3E%3Cg%20id%3D%22holder_17acafb068e%22%3E%3Crect%20width%3D%22283%22%20height%3D%22160%22%20fill%3D%22%23777%22%3E%3C%2Frect%3E%3Cg%3E%3Ctext%20x%3D%22105.6875%22%20y%3D%2286.24375%22%3E283x160%3C%2Ftext%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E",
      activeTab: "TabA",
    };
  },
  // methods vs computer
  // methods functions gulo jekono ekta function call a sob function re render hobe
  // computed er under a ekta function call a onno function re render hobena
  methods: {
    add() {
      return 1 + 1 + 1;
    },
    peramiterAdd(a, b, c) {
      return a + b + c;
    },
    add2(num) {
      return num + this.baseNumber;
    },
    increment() {
      if (this.count >= 0) {
        this.count += 1;
      }
    },
    decrement() {
      if (this.count > 0) {
        this.count -= 1;
      }
    },
    increment5(num, event) {
      if (this.count >= 0) {
        this.count += num;
      }
      console.log(event);
    },

    eventFunction(event) {
      alert("See console.......... !");
      console.log("event target :: ", event.target);
    },
    submitForm1() {
      alert("Submitted: see console..........");
      console.log("form Values", this.formValues);
    },
    changeComputedFullName2() {
      this.computedFullName2 = "Princes Diana";
    },
    closePopup(data) {
      this.showPopup = false;
      alert("prop data in Console");
      console.log("extra data from popup component, Name::", data);
    },
  },
  computed: {
    computedFullName() {
      return `${this.firstName2} ${this.lastName2}`;
    },
    computedFullName2: {
      get() {
        return `${this.firstName2} ${this.lastName2}`;
      },
      set(value) {
        const names = value.split(" ");
        this.firstName2 = names[0];
        this.lastName2 = names[1];
      },
    },
  },
  mounted() {
    console.log(
      "postList component ref instance, expand the target",
      this.$refs.userListRef
    );
    this.$refs.inputRef.focus();
    //this.$refs = vue property
    // inputRef= input ref name
  },

  watch: {
    count(newValue, oldValue) {
      if (newValue > oldValue && newValue === 5) {
        alert("count er crossing the value of  5");
      }
    },
    movie: {
      handler(newValue) {
        console.log(`Calling API with movie name = ${newValue}`);
      },
      immediate: true,
      // immediate= it means run immediate after loading the page
    },
    movieList: {
      handler(newValue) {
        console.log(
          `Calling API with movieList  = ${newValue.movie2} and ${newValue.movie3}`
        );
      },
      deep: true,
      // deep= without this line watcher will not run for object or array values (means  nested data)
    },
  },
  // watch: data er vitor initialize object gulo watcher funtion a call hole alltime sei object er updated data er todaroki hote thakbe . also set data er new value +old value sob catch korbe
  provide() {
    return {
      loggedInEmail: this.userEmail,
    };
  },
  //provide()=use for passing the props data, parent component to a down child of the tree ,child component can get this data by using inject object
  //loggedInEmail=property
  //this.userEmail = data() funtion's returned data
};
</script>

<style>
.centerContent {
  display: grid;
  place-items: center;
}

.className3 {
  background: yellow;
}

.className4 {
  color: red;
}

.className5 {
  color: green;
}

.underLine {
  text-decoration: underline;
}
</style>
