<template>
  <div class="boxBorder">
      <div> PostList component</div>
      <button @click="getUsers">Click For Post</button>
      <h4 v-if="errorMsg">{{errorMsg}}</h4>
      <div v-for="user in users" :key="user.id">
          <div>User Name: {{user.name}}</div>
      </div>
  </div>
</template>

<script>
import axios from "axios"
export default {
  name: "postList",
  created() {
    this.getUsers();
  },
  data(){
      return{
          users:[],
          errorMsg:''
      }
  },
  methods:{
      getUsers(){
        axios.get('https://jsonplaceholder.typicode.com/users')
        .then((response)=>{
            this.users=response.data
        })  
        .catch((error)=>{
            console.log(error)
            this.errorMsg="error During fetching data"
        })
      }
  }
};
</script>

