<template>
  <div>
    <input
      type="text"
      :value="modelValue"
      @InputExample="$emit('update:modelValue', $event.target.value)"
    />
  </div>
</template>

<script>
export default {
  name: "InputExample",
  props: {
    modelValue: String,
  }
};
</script>

