<template>
  <div>
    <div>NameList Component</div>
    <div class="text-info bg-dark" v-for="(name, index) in names" :key="index">
      <slot :firstName="name.firstName" :lastName="name.lastName"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "NameList",
  data() {
    return {
      names: [
        {
          firstName: "Princes",
          lastName: "Diana",
        }
      ],
    };
  },
};
</script>

