<template>
  <div class="boxBorder">
    <h3>This is a popup</h3>
    <input type="text" v-model="name" />
    
    <button class="btn btn-sm btn-info" @click="$emit('popupClose', name)">
      Close Popup
    </button>
  </div>
</template>

<script>
export default {
    name:"Popup",
    data(){
      return {
        name:''
      };
    },
    // ['popupClose']  or if we want to add function over this
    emits:{
      popupClose:(name)=>{
        // (Validating Emitted Events) works start
        if(!name){
          return false
        }else{
          return true
        }
      },
    }
};
</script>

